*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}

.container {
  position: relative;
  a{
    text-decoration: none;
    color: #4671F0;
    display: inline-block;
  }
  .grid {
    max-width: 1100px;
    margin: auto;
    .inside-area {
      display: flex; /* establish flex container */
      flex-direction: row; /* make main axis vertical */
      justify-content: center; /* center items vertically, in this case */
      align-items: center; /* center items horizontally, in this case */
      height: 100vh;
      .video{
        width: 50%;
        display: inline-block;
        position: relative;
        .info{
          text-align: center;
          padding-top: 10px;
          font-size: 14px;
        }
        .icon{
          position: absolute;
          left: 0;
          right: 0;
          margin: auto;
          bottom: 46px;
          text-align: center;
        }
        video{
          display: block;
          width: 100%;
          border-radius: 4px;
        }
      }
      .right-side{
        width: 50%;
        display: inline-block;
        text-align: center;
        .inner{
          width: 330px;
          margin: auto;
          text-align: center;
        }
        .info-p{
          font-size: 13px;
          text-align: center;
          padding-bottom: 30px;
        }
        .prime-btn{
          background-color: #4671F0;
          color: #ffffff;
          transition: all 0.2s ease-in-out 0s;
          width: 100%;
          display: block;
          padding: 16px 0;
          font-weight: 600;
          margin-bottom: 8px;
          &:hover{
            background-color: #0069d9;
          }
        }
        a.secondary{
          color: #4671F0;
          transition: all 0.2s ease-in-out 0s;
          border: 1px solid #4671F0;
          border-radius: 2px;
          padding: 10px;
          font-weight: 500;
          font-size: 14px;
          &:hover{
            border-color: #0069d9;
            background-color: #F8F6FF;
          }
        }
        a.secondary2{
          color: #4671F0;
          transition: all 0.2s ease-in-out 0s;
          border: 1px solid #4671F0;
          border-radius: 20px;
          padding: 10px 15px;
          font-weight: 500;
          font-size: 14px;
          &:hover{
            border-color: #0069d9;
            background-color: #F8F6FF;
          }
          img{
            position: relative;
            top: 3px;
            padding-right: 4px;
          }
        }
        .copy{
          margin-top: 20px;
          font-size: 14px;
          font-weight: 500;
          cursor: pointer;
          display: inline-block;
          img{
            position: relative;
            top: 2px;
            padding-right: 4px;
          }
        }
        .setting{
          border-top: 1px solid #E2E2E2;
          padding-top: 20px;
          margin-top: 20px;
        }
      }
    }
  }
  .footer{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    .inside{
      width: 90%;
      margin: auto;
      padding-top: 30px;
      .logo{
        width: 50%;
        display: inline-block
      }
      .cannot-btn{
        width: 50%;
        display: inline-block;
        text-align: right;
        vertical-align: top;
        a{
          text-decoration: none;
          border: 1px solid #E4665D;
          color: #E4665D;
          display: inline-block;
          padding: 6px 10px;
          font-size: 14px;
          border-radius: 2px;
          transition: all 0.2s ease-in-out 0s;
          &:hover{
            background-color: #E4665D;
            color: #ffffff;
          }
        }
      }
    }
  }
}
