gin-top:20px;font-size:14px;font-weight:500;cursor:pointer;display:inline-block}.container .grid .inside-area .right-side .copy img{position:relative;top:2px;padding-right:4px}.container .grid .inside-area .right-side .setting{border-top:1px solid #e2e2e2;padding-top:20px;margin-top:20px}.container .footer{position:fixed;left:0;right:0;top:0}.container .footer .inside{width:90%;margin:auto;padding-top:30px}.container .footer .inside .logo{width:50%;display:inline-block}.container .footer .inside .cannot-btn{width:50%;display:inline-block;text-align:right;vertical-align:top}.container .footer .inside .cannot-btn a{text-decoration:none;border:1px solid #e4665d;color:#e4665d;display:inline-block;padding:6px 10px;font-size:14px;border-radius:2px;transition:all .2s ease-in-out 0s}.container .footer .inside .cannot-btn a:hover{background-color:#e4665d;color:#fff}
